import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    abonos:[],
    capital:[],
    logeo: false,
    nombre:"",
    privilegios:"",
    idReporte:[],
    departamento:"",
    recupera:false,
    numero:"",
  },
  mutations: {
    mutalogueo(state, estado){
      state.logeo = estado
    },
    mutaNombre(state, nom){
      state.nombre = nom
    },
    mutaPrivilegios(state, privi){
      state.privilegios=privi
    },
    mutaIdReporte(state, id){
      state.idReporte = id
    },
    mutaDepartamento(state, depto){
      state.departamento = depto
    },
    mutaRecupera(state, recupera){
      state.recupera = recupera
    },
    mutaNumero(state, numero){
      state.numero = numero
    },
    mutaAbonos(state, abono){
      state.abonos = abono
    },
    mutaCapital(state, capital){
      state.capital = capital
    }
     
  },
  actions: {
    actionLogueo(contex, estado){
        contex.commit('mutalogueo', estado)
   },
    actionMutaNombre(contex, nom){
        contex.commit('mutaNombre', nom)
    },
    actionMutaPrivi(contex, pri){
        contex.commit('mutaPrivilegios', pri)
    },
    actionMutaProve(contex, bande){
      contex.commit('mutaProve', bande)
    },
    actionIdReporte(contex, id){
      contex.commit('mutaIdReporte', id)
    },
    actionDepartamento(contex, depto){
      contex.commit('mutaDepartamento', depto)
    },
    actionRecupera(contex, recupera){
      contex.commit('mutaRecupera', recupera)
    },
    actionNumero(contex, numero){
      contex.commit('mutaNumero', numero)
    },
    actionMutaAbonos(context, abono){
      context.commit('mutaAbonos', abono)
    },
    actionMutaCapital(context, capital){
      context.commit('mutaCapital', capital)
    },
  },
})
