<template>
    <v-row>
        <v-card  max-width="400" class="elevation-12">
            <v-toolbar color="teal lighten-4" dark>
            <v-toolbar-title>
                <ImgLiquida height="65" width="280" />
            </v-toolbar-title>
            <v-spacer />
            </v-toolbar>
            <v-card-text>
            <v-form ref="form" lazy-validation>
                <v-text-field
                v-model="email"
                :rules="emailRules"
                :counter="80"
                label="E-mail"
                prepend-icon="email"
                required
                />
                <v-text-field
                id="password"
                label="Contrasea"
                name="password"
                :counter="16"
                v-model="psw"
                prepend-icon="lock"
                type="password"
                />
                <v-text-field
                id="password"
                label="Re-Contrasea"
                :rules="[(psw === psw2) || 'La contrasea no es igual']"
                :counter="16"
                name="password"
                v-model="psw2"
                prepend-icon="lock"
                type="password"
                />
                <v-text-field
                v-model="name"
                label="Nombre"
                :rules="nameRules"
                :counter="80"
                prepend-icon="perm_identity"
                required
                />
                <v-select :items="items" v-model="plan" label="Planes disponibles" dense outlined />
            </v-form>

            <v-spacer></v-spacer>
            </v-card-text>

            <v-card-actions>
            <v-spacer></v-spacer>

            <v-btn color="teal accent-2" rounded @click="comprobar" :disabled="desactivado">Registrarme</v-btn>
            </v-card-actions>
        </v-card>
        <v-row justify="center">

            <v-dialog
            v-model="dialog"
            max-width="500"
            >
            <v-card class="elevation-12">
                <v-card-title class="headline"></v-card-title>

                <v-card-text>
                <vue-paycard :value-fields="valueFields"/>
                    <v-row>
                        <v-col md="6">
                            <v-text-field
                                v-model="tarjeta"
                                :counter="19"
                                :rules="tarRules"
                                label="Nmero de Tarjeta"
                                prepend-icon="payment"
                                v-mask="'#### #### #### ####'"
                                required
                                />
                        </v-col>
                        <v-col md="6">
                            <v-text-field
                                v-model="nombre"
                                :counter="25"
                                label="Numbre Completo"
                                prepend-icon="person"
                                required
                                />
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col md="4">
                            <v-select :items="items2" v-model="mes" label="Mes" dense outlined />
                        </v-col>
                        <v-col md="4">
                            <v-select :items="items3" v-model="anio" label="Ao" dense outlined />
                        </v-col>

                        <v-col md="4">
                            <v-text-field
                                v-model="cvv"
                                :counter="4"
                                label="CVV"
                                prepend-icon="payment"
                                v-mask="'####'"
                                required
                                />
                        </v-col>

                    </v-row>
                </v-card-text>

                <v-card-actions>
                <v-spacer></v-spacer>

                <v-btn
                    color="green darken-1"
                    text
                    @click="dialog = false"
                >
                    No acepto
                </v-btn>

                <v-btn
                    color="green darken-1"
                    text
                    @click="dialog = false"
                >
                    Acepto
                </v-btn>
                </v-card-actions>
            </v-card>
            </v-dialog>
        </v-row>

    </v-row>
</template>
<script>
import ImgLiquida from '@/components/icons/liquidando'
export default {
    components:{
        ImgLiquida,
    },
  data: () => ({
    desactivado:false,
    name: "",
    email: "",
    plan: "",
    items: ["PRUEBA", "BASICO", "AVANZADO"],
    items2: ['01','02','03','04','05','06','07','08','09','10','11','12'],
    items3:['2016','2017','2018','2019','2020','2021','2022','2023','2024','2025','2026','2027'],
    userRules: [
      (v) => !!v || "El usuario es requerido",
      (v) => (v && v.length <= 15) || "El usuario no debe exceder los 15 caracteres",
      (v) => (v && v.length >= 6) ||  "El usuario no debe ser menor a los 6 caracteres",
    ],
    nameRules: [
      (v) => !!v || "El Nombre es requerido",
      (v) => (v && v.length <= 80) || "El nombre no debe exceder los 80 caracteres",
      (v) => (v && v.length >= 6) ||  "El nombre no debe ser menor a los 6 caracteres",
    ],
    emailRules: [
      (v) => !!v || "E-mail es requerido",
      (v) => (v && v.length <= 40) || "El usuario no debe exceder los 40 caracteres",
      (v) => /.+@.+\..+/.test(v) || "E-mail debe ser valido correo@algo.com",
    ],
    psw: "",
    pswRules: [
      (v) => !!v || "La contrasea es requerida",
      (v) => (v && v.length >= 15) || "La contrasea no debe ser menor a los 6 caracteres",
      (v) => (v && v.length <= 15) || "La contrasea no debe exceder los 15 caracteres",
    ],
    psw2: "",
    dialog: false,
    tarjeta:"",
    nombre:"",
    mes:"",
    anio:"",
    cvv:"",
    valueFields: {
        cardName: '',
        cardNumber: '',
        cardMonth: '',
        cardYear: '',
        cardCvv: ''
      },
    tarRules: [
      (v) => !!v || "El nmero de tarjeta es requerido",
      (v) => (v && v.length >= 19) || "El nmero no debe exceder los 15 caracteres",
    ],
  }),
  watch: {
    plan(val) {
        if (val === "BASICO" || val ==="AVANZADO") {
            this.dialog = true
        }else{
            this.dialog = false
        }
    },
    tarjeta(val){this.valueFields.cardNumber = val},
    nombre(val){this.valueFields.cardName = val},
    mes(val){this.valueFields.cardMonth=val},
    anio(val){this.valueFields.cardYear=val},
    cvv(val){this.valueFields.cardCvv = val},
  },
  methods: {
    comprobar() {
        this.desactivado=true
        let lista = [
            this.psw,
            this.psw2,
            this.name,
            this.email,
            this.plan,
        ]
        let lisname = [
            "Contrasea",
            "Re-Contrasea",
            "Nombre",
            "Correo",
            "Plan",
        ]
        let tarjeta = [
            this.tarjeta,
            this.nombre,
            this.mes,
            this.anio,
            this.cvv,
        ]
        let tarjetaStrin = [
            "tarjeta",
            "nombre",
            "mes",
            "anio",
            "cvv",
        ]
        let listavacios=[]
        let tarvac=[]
        let bandera = 0
        let bande = 0
        lista.forEach(element => {
            if  (element.length==0){
                listavacios.push(lisname[bandera])
            }
            if (bandera==5){
                if (element=="BASICO" || element=="AVANZADO"){
                    tarjeta.forEach(element2=>{
                        if (element2.length==0){
                            tarvac.push(tarjetaStrin[bande])
                        }
                        bande += 1
                    })
                }else{
                    tarjeta=["PRUEBA",]
                }
            }
            bandera +=1
        });
        if(listavacios.length>0){
            this.$swal({
                icon:"error",
                title:"Campos Requeridos",
                text: listavacios+" "+" "+tarvac,
                showConfirmButton: true,
                timer: 4000,
            })
        }else {
            this.clickMe(lista, tarjeta)
        }
    },
    clickMe(data_user, data_pago) {
        this.axios.post('/usuarios/registro', {"dataUser":data_user,
                            "dataPago":data_pago,})
                .then(res => {
                    if(res.data){
                        if(res.data['data'] == 1){
                            this.$swal({icon: 'error',
                                title: 'Tenemos un problema!!',
                                text: "El Correo existe en la base de datos",
                                showConfirmButton: false,
                                timer: 4000,
                            })
                        }else if (res.data['data']==2){
                            this.$swal({icon: 'error',
                                title: 'Tenemos un problema!!',
                                text: "El usuario ya existe en la base de datos",
                                showConfirmButton: false,
                                timer: 4000,
                            })
                        }else if (res.data['data']==5){
                            this.$swal({icon: 'error',
                                title: 'Tenemos un problema!!',
                                text: "El correo que introdujo no existe o esta erroneo",
                                showConfirmButton: false,
                                timer: 4000,
                            })
                        }else if (res.data['data']==0){
                            this.$swal({icon: 'success',
                                title: 'Felicidades!!',
                                text: "El regsitro se agrego con exito a la base de datos",
                                showConfirmButton: false,
                                timer: 4000,
                            })
                            
                            this.psw=""
                            this.psw2=""
                            this.name=""
                            this.email=""
                            this.plan=""

                            this.tarjeta=""
                            this.nombre=""
                            this.mes=""
                            this.anio=""
                            this.cvv=""
                            this.$emit("valor_activacion", false)
                        }
                        
                    };
                })
                .catch(e => {
                    this.$swal({icon: 'error',
                        title: 'Tenemos un problema!!',
                        text: e.response.data,
                        showConfirmButton: false,
                        timer: 4000,
                        })
                })
    },
  },
}
</script>