<script>
  import ImgLiquida2 from '@/components/icons/liquidando2'
  import malmacen from '@/components/menuAlmacen';
  import jwtDecode from 'jwt-decode'

  export default {
    name: 'BarraMenu',
    components:{
      malmacen,
      ImgLiquida2,
    },
    data () {
      
      return{
        drawers: false,
        show: false,
        muestra:1,
        nombre:"",
         mini: true,
      }
    },
    methods:{
      logout(){
        localStorage.removeItem('userToken')
        localStorage.removeItem('login')
        this.$store.dispatch('actionLogueo', false)
        this.$store.dispatch('actionMutaNombre',"")
        this.$store.dispatch('actionMutaPrivi',"")
        this.$store.dispatch('actionIdReporte',[])
        this.$store.dispatch('actionDepartamento',"")
        this.$forceUpdate()
      }
    },
    created(){  // aqui defino el nombre que aparece en el avatar
      const token = localStorage.userToken
      const decoded = jwtDecode(token)
      this.$store.dispatch('actionMutaNombre', decoded.identity.nombre)
      this.$store.dispatch('actionMutaPrivi', decoded.identity.privilegios)
      this.$store.dispatch('actionDepartamento', decoded.identity.departamento)
      if (this.$store.state.nombre.split(" ").length==4){
        this.nombre=(this.$store.state.nombre.split(" "))[0]+" "+(this.$store.state.nombre.split(" "))[2]
      }else if(this.$store.state.nombre.split(" ").length==3){
        this.nombre=(this.$store.state.nombre.split(" "))[0]+" "+(this.$store.state.nombre.split(" "))[1]
      }
    }
  }
</script>