<template>
  <v-row>
   
      <v-divider></v-divider>

      <v-list v-if="privilegios==false">
        <v-list-item :to="items[0].enlace">
          <v-list-item-icon>
            <IconBase width="40" height="40" iconName='Inicio'><IconHome ></IconHome></IconBase>
          </v-list-item-icon>
        </v-list-item>
        <v-list-item :to="items[1].enlace">
          <v-list-item-icon>
            <IconBase width="40" height="40" iconName='Liquidacion'><IconLiquide/></IconBase>
          </v-list-item-icon>
        </v-list-item>
        <v-list-item :to="items[2].enlace">
          <v-list-item-icon>
            <IconBase width="40" height="40" iconName='Historial'><IconHistorial/></IconBase>
          </v-list-item-icon>
        </v-list-item>
      </v-list>
      <v-list v-else>
        <v-list-item :to="items[0].enlace">
          <v-list-item-icon>
            <IconBase width="40" height="40" iconName='Administracion'><IconAdmin/></IconBase>
          </v-list-item-icon>
        </v-list-item>
      </v-list>
  </v-row>

</template>

<script >
import IconBase from '@/components/IconBase.vue'
import IconHome from '@/components/icons/home.vue'
import IconLiquide from '@/components/icons/liquide.vue'
import IconHistorial from '@/components/icons/historial.vue'
import IconAdmin from '@/components/icons/admin.vue'
import jwtDecode from 'jwt-decode'

export default {
    name: 'opAlmacen',
    components: {
        IconBase,
        IconHome,
        IconLiquide,
        IconHistorial,
        IconAdmin,
    },
    data () {
      return{
        drawers: false,
        show: false,
        items: [],
        drawer: true,
        mini: true,
        privilegios:false,
      }
    },
    mounted(){
      const token = localStorage.userToken
      const decoded = jwtDecode(token)
      var rol = decoded.identity.privilegios
      console.log(rol)
      if (rol == "1.1.1.1.1"){
        this.privilegios = true
        this.items.push({ title: 'Admin', enlace:'/'})
      }else{
        this.items.push(
          { title: 'Home', enlace:'/' },
          { title: 'Liquide',  enlace:'/calculos/amortiza' },
          { title: 'Historial', enlace: '/calculos/historial'},
        )
      }
    },
  }

</script>