<template>
    <v-container>
        <!-- Dialogo de login -->
            <v-dialog
                v-model="aparece"
                max-width="450"
                height="450"
            >
                    <v-container
                        fluid
                    >
                        <v-row
                        align="center"
                        justify="center"
                        
                        >
                            <v-card class="elevation-12">
                                <v-toolbar
                                    color="teal lighten-4"
                                    dark
                                >
                                    <v-toolbar-title><ImgLiquidando height="65" width="280" /></v-toolbar-title>
                                    <v-spacer />
                                    
                                </v-toolbar>
                            <v-card-text>
                                <v-form>
                                <v-text-field
                                    label="Email"
                                    name="login"
                                    v-model="user"
                                    prepend-icon="alternate_email"
                                    type="text"
                                />

                                <v-text-field
                                    id="password"
                                    label="Contrasea"
                                    name="password"
                                    v-model="psw"
                                    prepend-icon="lock"
                                    type="password"
                                    @keyup.prevent.enter="clickMe"
                                />
                                </v-form>
                                    <v-spacer></v-spacer>
                                
                            </v-card-text>
                            
                            <v-card-actions>
                                <v-spacer />
                                <v-btn text small rounded @click.stop="dialog = true" color="primary">Olvidaste tu contrasea?</v-btn>
                                <v-btn @click="clickMe" color="primary"><v-icon>lock_open</v-icon> Acceder</v-btn>
                            </v-card-actions>
                            </v-card>

                        </v-row>
                    </v-container>
            </v-dialog>
            <!-- Dialogo de login -->
        <v-layout>
            <v-content>
                <v-app-bar
                app
                color="teal lighten-5">
                <!-- src="https://picsum.photos/1920/1080?random"> -->
                    <ImgLiquidando2 height="150" width="175" />
                    <v-spacer></v-spacer>
                    <v-spacer></v-spacer>
                    <v-btn dark text outlined rounded color="teal lighten-2" @click="aparece2=!aparece2" > Registrate</v-btn>
                    <v-btn dark outlined rounded color="teal lighten-2" @click="aparece=!aparece" >Iniciar session</v-btn>
                </v-app-bar>
            </v-content>
        </v-layout>
        <v-row justify="center">
            <v-dialog
            v-model="aparece2"
            max-width="300"
            >
                <registro @valor_activacion="aparece2=$event"/>
            </v-dialog>
        </v-row>
        <v-row justify="center">
            <v-dialog
            v-model="dialog"
            width="450"
            height="450"
            >
            <v-card class="elevation-12">
                                <v-toolbar
                                    color="teal lighten-4"
                                    dark
                                >
                                    <v-toolbar-title><ImgLiquidando height="65" width="280" /></v-toolbar-title>
                                    <v-spacer />
                                    
                                </v-toolbar>
                            <v-card-text>
                                <v-form>
                                    <v-text-field
                                    label="Confirme su correo Electronico"
                                    name="email"
                                    v-model="correoe"
                                    prepend-icon="alternate_email"
                                    type="text"
                                />

                                </v-form>
                                    <v-spacer></v-spacer>
                                
                            </v-card-text>

                <v-card-actions>
                <v-spacer></v-spacer>

                <v-btn
                    color="deep-orange accent-2"
                    rounded
                    @click="cerrar"
                >
                    Cancelar
                </v-btn>

                <v-btn
                    color="teal accent-2"
                    rounded
                    @click="clickMe2"
                >
                    Aceptar
                </v-btn>
                </v-card-actions>
            </v-card>
            </v-dialog>
        </v-row>
        <v-layout>
            <Carrusel/>
        </v-layout>
    <v-layout>
        <v-container>
            <v-layout>
                <Pasos/>
            </v-layout>
            <v-layout>
                <Realmente/>
            </v-layout>
            <v-layout>
                <Beneficios/>
            </v-layout>
            <v-layout pa-5>
                <Planes/>
            </v-layout>
            <v-layout >
                <v-card color="proyecto">
                    <v-row class="mb6">
                        <v-col justify="center" pa-5 ma-5>
                            <Contacto1/>
                        </v-col>
                        <v-col justify="center" pa-5 ma-5>
                            <Contacto2/>
                        </v-col>
                        <v-col justify="center" pa-5 ma-5>
                            <Contacto3/>
                        </v-col>
                    </v-row>
                </v-card>
            </v-layout>
        </v-container>
    </v-layout>
    </v-container>
</template>
<script>
  import registro from'@/components/usuarios/registro'
  import IconGratis from '@/components/icons/gratis'
  import IconBasico from '@/components/icons/basico'
  import IconAvanzado from '@/components/icons/avanzado'
  import Planes from '@/components/login/planes'
  import Pasos from '@/components/login/pasos'
  import Carrusel from '@/components/login/carrusel'
  import Beneficios from '@/components/login/beneficios'
  import Realmente from '@/components/login/realmente'
  import Contacto1 from '@/components/login/contacto1'
  import Contacto2 from '@/components/login/contacto2'
  import Contacto3 from '@/components/login/contacto3'
  import ImgLiquidando from '@/components/icons/liquidando'
  import ImgLiquidando2 from '@/components/icons/liquidando2'

  export default {
      components: {
          registro,
          IconGratis,
          IconBasico,
          IconAvanzado,
          Planes,
          Pasos,
          Carrusel,
          Beneficios,
          Realmente,
          Contacto1,
          Contacto2,
          Contacto3,
          ImgLiquidando,
          ImgLiquidando2,
      },
    data (){
        return{
            aparece2:false,
            user:"",
            psw:"",
            aparece:false,
            dialog: false,
            
            characters: [
                {
                    name: "Lowercase",
                    value: "abcdefghijklmnopqrstuvwxyz",
                    checked: true,
                },
                {
                    name: "Uppercase",
                    value: "ABCDEFGHIJKLMNOPQRSTUVWXYZ",
                    checked: true,
                },
                {
                    name: "Numbers",
                    value: "0123456789",
                    checked: true,
                },
                {
                    name: "Special Characters",
                    value: "_-+=)(*&^%$#@!`~",
                    checked: false,
                },
                ],
        password: "",
        gLength: 12,
        correoe:"",
      }        
    },
    methods:{
        clickMe(){    // aqui hago login y traigo el token         
            this.axios.post('/login', {"user":this.user,
                            "psw":this.psw,})
                .then(res => {
                    if(res.data){
                        if(res.data['error']=="Datos Invalidos"){
                            this.$swal({icon: 'error',
                                title: 'Tenemos un problema!!',
                                text: "Usuario o Password Incorrectos, por favor verifiquelos, e intente de nuevo",
                                showConfirmButton: false,
                                timer: 4000,
                            });
                            this.user = ''
                            this.psw = ''
                        }else if(res.data['error']=="cambio"){
                            this.$swal({icon: 'error',
                                title: 'Tenemos un problema!!',
                                text: "El usuario ha solicitado un cambio de contrasea, y se le ha enviado un email con sus nuevas credenciales",
                                showConfirmButton: false,
                                timer: 4000,
                            });
                            this.user = ''
                            this.psw = ''
                        }else{
                            localStorage.setItem('userToken', res.data)
                            localStorage.setItem('login', true)                   
                            this.user=""
                            this.psw=""
                            this.$store.dispatch('actionLogueo', true)
                            this.aparece=false
                        }
                    };
                })
                .catch(e => {
                    this.$swal({icon: 'error',
                        title: 'Tenemos un problema!!',
                        text: e.response.data,
                        showConfirmButton: false,
                        timer: 4000,
                        })
                })
        },
        clickMe2(){
            // dialogo de confirmacin.
            if (this.correoe){
                this.$swal({
                    title: 'Est Seguro?',
                    text: "Al precionar en Si, se cambiar su contrasea por una temporal y debra cambiarla de nuevo al ingresar al sistema",
                    icon: 'warning',
                    padding: '3em',
                    background: '#fff url(../imgs/daylight-forest-glossy-lake-443446.jpg)',
                    backdrop: `
                        rgba(0,0,123,0.4)
                        url("../imgs/daylight-forest-glossy-lake-443446.jpgg")
                        left top
                        no-repeat
                    `,
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: 'Si, Procede!'
                    }).then((result) => {
                    if (result.value) {
                        this.cambiado(this.correoe)
                    }
                    
                    })
            }else{
                this.$swal({icon: 'error',
                        title: 'Tenemos un problema!!',
                        text: "El campo email est vacio",
                        showConfirmButton: false,
                        timer: 4000,
                        })
            }
        },
        pswRandom () {
          let result = ""
          let charactersVal = ""
          for (var j = 0; j < this.characters.length; j++) {
            if (this.characters[j].checked) {
              charactersVal += this.characters[j].value
            }
          }
          for ( var i = 0; i < this.gLength; i++ ) {
            result += charactersVal.charAt(Math.floor(Math.random() * charactersVal.length))
          }
          this.password = result
        },
        cerrar () {
            this.correoe=""
            this.dialog=false
        },
        async cambiado (text_email) {
            this.pswRandom()
            await this.axios.post('/recupera', {"email":text_email, "psw":this.password })
                .then(res => {
                    if(res.data){
                        this.dialog=false
                        if (res.data['data']){
                            this.correoe=""
                            this.aparece=false
                            this.$swal(
                                    'Realizado!',
                                    'Hemos Cambiado su contrasea.',
                                    'success'
                                    )
                        }else{
                            this.correoe=""
                            this.$swal({icon: 'error',
                                title: 'Tenemos un problema!!',
                                text: "El correo no existe",
                                showConfirmButton: false,
                                timer: 4000,
                                })
                        }
                    }
                })
                .catch(e => {
                    this.$swal({icon: 'error',
                        title: 'Tenemos un problema!!',
                        text: e.response.data,
                        showConfirmButton: false,
                        timer: 4000,
                        })
                })
            

        }
    },
    
  }
</script>